}

mat-slide-toggle .mat-slide-toggle-bar {
  height: 24px;
  border-radius: 12px;
}

mat-slide-toggle .mat-slide-toggle-thumb-container {
  top: 2px;
  left: 2px;
}

mat-slide-toggle.mat-slide-toggle.mat-checked .mat-slide-toggle-thumb-container {
  transform: translate3d(12px, 0, 0);
}

#placeos mat-pseudo-checkbox.mat-pseudo-checkbox-checked {
  background: var(--primary);
}

#placeos mat-checkbox .mat-checkbox-inner-container {
  margin-left: 8px;
}

#placeos mat-checkbox.mat-checkbox-checked .mat-checkbox-background {
  background-color: var(--primary);
}

[bg=white] .mat-form-field-outline {
  background-color: #fff;
}

mat-dialog-container.mat-dialog-container {
  padding: 0;
}

mat-dialog-container.mat-dialog-container header {
  display: flex;
  align-items: center;
  padding: 0.5em 1em;
  width: 100%;
  background-color: var(--primary);
  color: var(--font-light);
}

mat-dialog-container.mat-dialog-container header h1,
mat-dialog-container.mat-dialog-container header h2,
mat-dialog-container.mat-dialog-container header h3,
mat-dialog-container.mat-dialog-container header h4 {
  flex: 1;
  min-width: 50%;
}

mat-dialog-container.mat-dialog-container header > * {
  margin: 0;
}

mat-dialog-container.mat-dialog-container .mat-dialog-content {
  margin: 0;
  padding: 0;
}

mat-dialog-container.mat-dialog-container .mat-dialog-content .body {
  padding: 1em;
}

mat-dialog-container.mat-dialog-container .mat-dialog-actions {
  margin-bottom: 0;
}

mat-dialog-container.mat-dialog-container .mat-dialog-actions footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

mat-form-field.mat-form-field-appearance-outline.mat-focused .mat-form-field-outline-thick {
  color: var(--primary);
}

mat-form-field.map .mat-form-field-wrapper, mat-form-field.white .mat-form-field-wrapper {
  padding: 0;
}

mat-form-field.map .mat-form-field-outline, mat-form-field.white .mat-form-field-outline {
  background-color: #fff;
  border-radius: 4px;
}

[hide-outline] .mat-form-field-wrapper {
  padding: 0;
}

.mat-form-field-infix {
  border-top: none;
}

.mat-select-panel .mat-option.mat-selected:not(.mat-option-disabled) {
  color: var(--primary);
}

mat-select .mat-select-trigger .mat-select-arrow-wrapper {
  transform: none;
}

.mat-simple-snackbar button.mat-button {
  background: none;
  border: none;
}

.mat-snack-bar-container.success {
  background-color: var(--success);
  color: #fff;
}

.mat-snack-bar-container.warn {
  background-color: var(--pending);
  color: #fff;
}

.mat-snack-bar-container.error {
  background-color: var(--error);
  color: #fff;
}

.mat-snack-bar-container.info {
  background-color: #37474f;
  color: #fff;
}

.mat-form-field.visible .mat-select-value {
  overflow: visible;
}

.mat-form-field .mat-form-field-subscript-wrapper {
  overflow: visible;
}

.mat-form-field .mat-form-field-wrapper .mat-form-field-label {
  top: 2em;
  margin-top: -0.1em;
}

.mat-form-field[overlay] .mat-form-field-outline, .mat-form-field[overlay]:hover .mat-form-field-outline {
  background-color: #fff;
  border-radius: 4px;
}

.mat-form-field.rounded .mat-form-field-outline, .mat-form-field.rounded:hover .mat-form-field-outline {
  border-radius: 2em;
}

.mat-form-field.rounded .mat-form-field-outline-start, .mat-form-field.rounded:hover .mat-form-field-outline-start {
  border-radius: 2em 0 0 2em;
  min-width: 2em;
}

.mat-form-field.rounded .mat-form-field-outline-end, .mat-form-field.rounded:hover .mat-form-field-outline-end {
  border-radius: 0 2em 2em 0;
}

.mat-form-field .mat-form-field-should-float .mat-form-field-wrapper .mat-form-field-label {
  transform: translateY(-1.5em) scale(0.75) !important;
}

mat-slider[white].mat-slider-min-value:not(.mat-slider-thumb-label-showing) .mat-slider-thumb {
  border-color: rgba(255, 255, 255, 0.26);
}

mat-slider[white] .mat-slider-track-background {
  background-color: rgba(255, 255, 255, 0.26);
}

mat-slider .mat-slider-thumb {
  background-color: var(--primary);
}

mat-slider .mat-slider-track-fill {
  background-color: var(--primary);
}

mat-form-field.mat-form-field-appearance-outline.mat-focused .mat-form-field-outline-thick {
  color: var(--primary);
}

[hide-outline] mat-form-field .mat-form-field-outline > * {
  border-color: rgba(0, 0, 0, 0);
}

[hide-outline] mat-form-field:hover .mat-form-field-outline > *, [hide-outline] mat-form-field.mat-focused .mat-form-field-outline > * {
  border-color: inherit;
}

[no-wrapper] mat-form-field .mat-form-field-wrapper {
  padding: 0;
  margin: 0;
}

.mat-button-toggle-button[aria-pressed=true] {
  background-color: var(--primary);
  color: var(--font-light);
}

.mat-chip.mat-standard-chip.mat-primary {
  color: var(--primary);
}

.mat-chip.mat-standard-chip.mat-accent {
  background-color: var(--pending);
}

#placeos mat-chip[color=internal] {
  color: var(--font-light);
  background-color: var(--primary);
}

#placeos mat-chip[color=external] {
  color: var(--font-light);
  background-color: var(--pending);
}

.mat-spinner circle {
  stroke: var(--primary);
}

.mat-progress-bar-fill::after {
  background-color: var(--primary);
}

div.mat-menu-panel {
  max-width: calc(100vw - 1em);
}

div.mat-menu-panel .desk {
  line-height: 48px;
  padding-left: 8px;
  padding-right: 8px;
}

div.mat-menu-panel .desk span {
  font-weight: 400;
  text-transform: capitalize;
}

.mat-tooltip {
  font-size: 0.8em;
  background-color: rgba(33, 33, 33, 0.85);
}

.mat-table td.mat-cell:last-of-type {
  padding-right: 0;
}

a-date-picker .date.active {
  background-color: var(--primary) !important;
}

a-date-picker .dot {
  background-color: var(--primary) !important;
}

.chevron {
  position: relative;
}

.chevron > div {
  position: relative;
  top: -2px;
  height: 12px;
  width: 12px;
  transform: rotate(45deg);
  transition: transform 200ms, top 200ms;
  border-bottom: 3px solid #000;
  border-right: 3px solid #000;
}

.chevron.upside > div {
  top: 2px;
  transform: rotate(225deg);
}

form mat-form-field,
form a-date-field,
form an-action-field,
form editor {
  flex: 1;
  min-width: 50%;
}

[disabled] .chevron {
  opacity: 0.35;
}

.mat-menu-content .legend {
  padding-left: 8px;
  display: flex;
  align-items: center;
  line-height: 2em;
}

.mat-menu-content .legend .color {
  border-radius: 50%;
  height: 10px;
  width: 10px;
  margin-right: 5px;
}

#placeos .mat-datepicker-toggle button {
  position: relative;
  margin-top: -1rem;
  top: 0.25rem;
}

[qr-code] {
  font-size: 6vmin;
}

[qr-code] h2 {
  font-size: 1.5em;
  margin: 0;
  padding: 0;
}

[qr-code] span {
  font-weight: 500;
}

[qr-code].space-y-2 > * + * {
  margin-top: 0.5em;
}

[qr-code] img {
  height: 70vmin;
  width: 70vmin;
}
input,
textarea {
  -webkit-user-select: text;
          user-select: text;
  background: none;
}

label {
  width: 100%;
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
  min-width: 100%;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

label > span:first-child {
  color: var(--pending);
}

label.error {
  color: var(--error);
}

#placeos .svg-viewer__label {
  min-width: 12rem;
}
.bg-darken {
  background-color: rgba(0, 0, 0, 0.1);
}

.text-light {
  color: rgba(255, 255, 255, 0.85);
}

.text-light-fade {
  color: rgba(255, 255, 255, 0.6);
}

.text-light-disabled {
  color: rgba(255, 255, 255, 0.35);
}

.text-dark {
  color: rgba(0, 0, 0, 0.85);
}

.text-dark-fade {
  color: rgba(0, 0, 0, 0.6);
}

.h-half {
  height: 50%;
}

.min-w-24 {
  min-width: 6em;
}

.min-w-32 {
  min-width: 8em !important;
}

.min-w-48 {
  min-width: 12em;
}

.min-w-64 {
  min-width: 16em;
}

.width-tablet {
  width: 768px;
  max-width: calc(100vw - 2em);
}

.top {
  top: 0 !important;
  bottom: auto !important;
}

.bottom {
  top: auto !important;
  bottom: 0 !important;
}

.left {
  left: 0 !important;
  right: auto !important;
}

.right {
  left: auto !important;
  right: 0 !important;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
